
export default function ListItem(props) {
    return(
        <div className="border flex items-center mb-4 bg-white p-2">
        <div>
            <img className="w-20 h-20" src={props.cover} alt="" />
        </div>
        <div className="ml-5">
            <h1 className="font-semibold break-all text-gray-600">{props.judul}</h1>
            <p className="text-xs text-white">{props.penulis}</p>
            <p className="text-sm">{props.penerbit}</p>
            <p className="text-xs text-gray-600">Tahun {props.tahun}, {props.ketebalan} Halaman</p>
        </div>
    </div>
    )
}