import React from "react";
import AdminLyout from "../../layout/admin_layout";
import axios from "axios";
import swal from "sweetalert";
import { useHistory } from "react-router-dom";
import {
  Header,
  Main,
  Footer,
  ListItem,
  Loading,
  Button,
} from "../../component";

export default function DaftarBuku(props) {
  const [isFetching, setIsFetching] = React.useState(true);
  setTimeout(() => {
    setIsFetching(false);
  }, 2000);
  const history = useHistory();

  const [books, setbooks] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(true);
  const getBooks = async () => {
    const url =
      "https://api-react-2.herokuapp.com/api/perpustakaan?keyword=&kode=27221";
    try {
      let respond = await axios.get(url);
      console.log(respond.data.data.data);
      setbooks(respond.data.data.data);
      setIsLoading(false);
    } catch (err) {
      console.log(err, "error");
      setIsLoading(false);
    }
  };
  React.useEffect(() => {
    getBooks();
  }, []);
  return (
    <AdminLyout>
      <Header>
        <div className="relative p-5">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 absolute text-black top-7 left-8"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            />
          </svg>
          <input
            className="w-full h-10 rounded-sm pl-12"
            type="text"
            placeholder="Cari Buku Kesukaan . . ."
          />
        </div>
      </Header>

      <Main>
        {" "}
        <h1 className="font-bold text-lg  mb-5">
          List Buku Yang Tersesadia
        </h1>{" "}
        {isFetching ? <Loading></Loading> : <ListItem></ListItem>}
        {isLoading ? (
          <React.Fragment>
            <Loading></Loading>
            <Loading></Loading>
            <Loading></Loading>
          </React.Fragment>
        ) : (
          books.map((key, i) => (
            
            <div key={key} className="border flex items-center mb-4 bg-[#F8F8F8] p-2">
              <div>
                <img className="w-20 h-24" src={i.cover} alt="" />
              </div>
              <div className="ml-5">
                <h1 className="font-semibold break-all text-[#303030]">
                  {i.judul_buku}
                </h1>
                <p className="text-xs text-[#B7B7B7]">{i.penulis}</p>
                <p className="text-sm">{i.penerbit}</p>
                <p className="text-xs text-[#545454]">
                  Tahun {i.tahun}, {i.ketebalan} Halaman
                </p>
              </div>
            </div>
          ))
          //
        )}
      </Main>

      <Footer>
        <Button
          className="bg-blue-700"
          onClick={() => history.push("/tambah-buku")}
        >
          {" "}
          Tambah Buku Kesukaan
        </Button>
        {/* <button onClick={history}
      type={"submit"}
      className="w-full py-4 bg-blue-600 rounded-lg font-bold">
          lmao
        </button> */}
      </Footer>
    </AdminLyout>
  );
}
